import { STICKER_CODE_REG } from '../regexp';
import { SymbolStyleProperties, ListProperties } from '../../utils/types';
import { mapping } from '../../utils/listStyleMapping';

export interface GetListDataParams {
  type: 'ul' | 'ol' | 'tlist';
  level: number;
  listId: string;
  isChecked?: boolean;
  listStyleType?: string;
  symbolStyle?: SymbolStyleProperties;
}

/**
 * 根据指定的参数生成合法的 list 数据
 *
 * @export
 * @param {GetListDataParams} { type, level, listId, isChecked, listStyleType, symbolStyle }
 * @returns {{ list: ListProperties }}
 */
export default function getListData({
  type,
  level,
  listId,
  isChecked,
  listStyleType,
  symbolStyle,
}: GetListDataParams): { list: ListProperties } {
  const data: ListProperties = {
    listId,
    level,
    isOrdered: type === 'ol',
    isTaskList: type === 'tlist',
    isChecked: type === 'tlist' ? !!isChecked : undefined,
    listStyleType,
    symbolStyle,
  };
  if (listStyleType) {
    if (mapping[listStyleType]) {
      const [format, text, align] = mapping[listStyleType][0]; // 默认取第一组
      data.listStyle = {
        format,
        text,
        align,
      };
    } else if (STICKER_CODE_REG.test(listStyleType)) {
      data.listStyle = {
        format: 'bullet',
        text: listStyleType,
        align: 'start',
      };
    }
  }

  return {
    list: data,
  };
}
